.cells {
  position: relative;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 40px (calc((100% - 50px) / 11)) [11];
  grid-template-columns: 40px repeat(11, calc((100% - 50px) / 11));
  -ms-grid-rows: (25px) [21];
  grid-template-rows: repeat(21, 25px);
  grid-gap: 1px;
  background: #cdcdcd;
  grid-auto-flow: dense;
  max-width: 100%;
  overflow: hidden;
}

.cells__spacer {
  background: #e6e6e6;
  position: relative;
}

.cells__spacer:after {
  content: "";
  position: absolute;
  right: 4px;
  bottom: 4px;
  height: 80%;
  width: 100%;
  background: linear-gradient(
    135deg,
    transparent 30px,
    #bbb 30px,
    #bbb 55px,
    transparent 55px
  );
}

.cells__alphabet {
  background: #e6e6e6;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.cells__number {
  background: #e6e6e6;
  -ms-grid-column: 1;
  -ms-grid-column-span: 1;
  grid-column: 1 / span 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.cells__input {
  border: none;
  padding: 0 6px;
}
